@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
:root{
  --primary-color: #263238;
  --card-color: #607D8B;
  --default-top: 2em;
  font-family: 'Open Sans', sans-serif;
}
*{
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
main{
  width: 80%;
  margin: auto;
}
.loader{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 80vh;
  text-align: center;
}


nav{
  background-color: var(--primary-color);
  min-height: 10vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: white;
}
nav a{
  color: white;
  text-decoration: none;
}
nav a:hover{
  text-decoration: underline;
  text-underline-position: under;
}
nav ul{
  list-style-type: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
nav ul li{
  margin-right: .9em;
}
nav h1{
  cursor: pointer;
}
.mobile-menu{
  display: none;
}
@media(max-width: 900px){
nav .nav-item{
    display: none;
}
nav .mobile-menu{
    display: block;
}
nav .mobile-menu .menu{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: space-evenly;
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-transition: all .3s;
    transition: all .3s;
    -webkit-transform: translateY(-300px);
            transform: translateY(-300px);
    position: absolute;
    left: 0;
    top: 0;
    background-color: black;
    width: 100%;
    height: 20vh;
}
}


footer{
	color: white;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	width: 100%;
	min-height: 10vh;
	background-color: var(--primary-color);
}
footer h1{
	cursor: pointer;
}

.background{
