  justify-items: center;
  -webkit-column-gap: .9em;
     -moz-column-gap: .9em;
          column-gap: .9em;
}
.total-case{
  background-color: var(--card-color);
  min-height: 50vh;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  padding: .9em;
