	display: grid;
	-webkit-column-gap: .9em;
	   -moz-column-gap: .9em;
	        column-gap: .9em;
	row-gap: .9em;
	grid-template-columns: 1fr 1fr 1fr;
	justify-items: center;
}
.countries-card{
	display: -webkit-box;
	display: -ms-flexbox;
